/* index.css (partial) */

/* Modern Font - Switch to Inter */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

/* Global resets or custom styles can go here */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Base variables for shared properties */
:root {
  --transition-speed: 0.3s;
  --border-radius: 8px;
  --font-main: 'Inter', sans-serif;
  
  /* Runway-inspired color palette */
  --primary-color: #0066FF;
  --primary-dark: #0055d4;
  --secondary-color: #FF4D3C;
  
  /* Light theme colors */
  --bg-color-light: #FFFFFF;
  --surface-color-light: #F8F8F8;
  --card-bg-color-light: #FFFFFF;
  --card-border-color-light: rgba(0, 0, 0, 0.08);
  --card-hover-shadow-light: rgba(0, 0, 0, 0.1);
  --card-selected-border-light: var(--primary-color);
  --text-primary-light: #111111;
  --text-secondary-light: #555555;
  --text-tertiary-light: #888888;
  --nav-bg-color-light: #FFFFFF;
  --nav-text-color-light: #555555;
  --text-on-primary-light: #FFFFFF;
  --text-on-secondary-light: #FFFFFF;
  --project-item-bg-light: #FFFFFF;
  --project-item-hover-light: #F5F5F5;
  --accent-light: var(--primary-color);
  --accent-soft-light: rgba(0, 102, 255, 0.1);
  --border-color-light: #EEEEEE;
  
  /* Dark theme colors */
  --bg-color-dark: #0F0F0F;
  --surface-color-dark: #1A1A1A;
  --card-bg-color-dark: #1A1A1A;
  --card-border-color-dark: rgba(255, 255, 255, 0.1);
  --card-hover-shadow-dark: rgba(0, 0, 0, 0.3);
  --card-selected-border-dark: var(--primary-color);
  --text-primary-dark: #FFFFFF;
  --text-secondary-dark: #BBBBBB;
  --text-tertiary-dark: #888888;
  --nav-bg-color-dark: #151515;
  --nav-text-color-dark: #CCCCCC;
  --text-on-primary-dark: #FFFFFF;
  --text-on-secondary-dark: #FFFFFF;
  --project-item-bg-dark: #222222;
  --project-item-hover-dark: #2A2A2A;
  --accent-dark: var(--primary-color);
  --accent-soft-dark: rgba(0, 102, 255, 0.2);
  --border-color-dark: #333333;
  
  /* Fraud detection dashboard specific colors */
  --fraud-normal-color: var(--primary-color);
  --fraud-risk-color: var(--secondary-color);
  --fraud-alert-color: #FFBB00;
}

/* Light Theme: "Minimal Monochromatic" */
.light-theme {
  /* Primary palette - blue */
  --primary-color: #4361ee;
  --primary-light: #6f86ff;
  --primary-dark: #2f45b5;
  
  /* Monochromatic backgrounds */
  --bg-color: var(--bg-color-light);
  --surface-color: var(--surface-color-light);
  --surface-hover: #f2f2f2;
  
  /* Text colors */
  --text-primary: var(--text-primary-light);
  --text-secondary: var(--text-secondary-light);
  --text-on-primary: var(--text-on-primary-light);
  --text-color-medium: #606060; /* Added medium text color */
  
  /* Border & shadow */
  --border-color: var(--border-color-light);
  --shadow-color: rgba(0, 0, 0, 0.06);
  
  /* Alias for backwards compatibility - to be used in refactoring */
  --secondary-color: var(--primary-light);
  --accent-color: var(--primary-dark);
  --text-color: var(--text-primary);
  --nav-bg-color: var(--nav-bg-color-light);
  --nav-text-color: var(--nav-text-color-light);
  --card-bg-color: var(--card-bg-color-light);
  --card-border-color: var(--card-border-color-light);
  --card-hover-shadow: var(--card-hover-shadow-light);
  --card-selected-border: var(--card-selected-border-light);
  --project-item-bg: var(--project-item-bg-light);
  --project-item-hover: var(--project-item-hover-light);
  --project-details-bg: var(--surface-color);
  --accent: var(--accent-light);
  --accent-soft: var(--accent-soft-light);
  --border-color: var(--border-color-light);
  --invert-icon: 0%;
}

/* Dark Theme: "Refined Dark" */
.dark-theme {
  /* Primary palette - blue */
  --primary-color: #4361ee;
  --primary-light: #738aff;
  --primary-dark: #364fc0;
  
  /* Monochromatic backgrounds */
  --bg-color: var(--bg-color-dark);
  --surface-color: var(--surface-color-dark);
  --surface-hover: #2d2d2d;
  
  /* Text colors */
  --text-primary: var(--text-primary-dark);
  --text-secondary: var(--text-secondary-dark);
  --text-on-primary: var(--text-on-primary-dark);
  --text-color-medium: #b0b0b0; /* Added medium text color */
  
  /* Border & shadow */
  --border-color: var(--border-color-dark);
  --shadow-color: rgba(0, 0, 0, 0.2);
  
  /* Alias for backwards compatibility - to be used in refactoring */
  --secondary-color: var(--primary-light);
  --accent-color: var(--primary-dark);
  --text-color: var(--text-primary);
  --nav-bg-color: var(--nav-bg-color-dark);
  --nav-text-color: var(--nav-text-color-dark);
  --card-bg-color: var(--card-bg-color-dark);
  --card-border-color: var(--card-border-color-dark);
  --card-hover-shadow: var(--card-hover-shadow-dark);
  --card-selected-border: var(--card-selected-border-dark);
  --project-item-bg: var(--project-item-bg-dark);
  --project-item-hover: var(--project-item-hover-dark);
  --project-details-bg: var(--surface-color);
  --accent: var(--accent-dark);
  --accent-soft: var(--accent-soft-dark);
  --border-color: var(--border-color-dark);
  --invert-icon: 100%;
}

/* Now style the app container and child elements using these vars */
.app-container {
  display: flex;
  min-height: 100vh;
  font-family: var(--font-main);
  background-color: var(--bg-color);
  color: var(--text-color);
  line-height: 1.6;
  position: relative;
}

body {
  margin: 0;
  font-family: var(--font-main);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  line-height: 1.3;
  margin-bottom: 1rem;
  letter-spacing: -0.02em;
}

h1 {
  font-size: 2.5rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.25rem;
}

p {
  margin-bottom: 1rem;
  color: var(--text-secondary);
}

/* Base link styles */
a {
  color: var(--primary-color);
  text-decoration: none;
  transition: color 0.25s ease;
}

a:hover {
  color: var(--primary-dark);
  text-decoration: none;
}

/* Dark theme link override */
.dark-theme a {
  color: rgba(255, 255, 255, 0.75);
}

.dark-theme a:hover {
  color: var(--primary-color);
}

/* Improved navbar with subtle glassmorphism effect */
.navbar {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 240px; /* Reduced from 280px */
  background-color: var(--nav-bg-color);
  padding: 1.5rem; /* Reduced from 2rem */
  box-shadow: none;
  position: sticky;
  top: 0;
  height: 100vh;
  backdrop-filter: none;
  border-right: 1px solid var(--card-border-color);
}

.navbar h1 {
  color: var(--text-primary);
  font-size: 1.6rem; /* Reduced from 1.8rem */
  margin-bottom: 2rem; /* Kept as 2rem for spacing */
  font-weight: 600;
  position: relative;
  letter-spacing: -0.03em;
}

.navbar h1::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 24px; /* Reduced from 36px for a more minimal look */
  height: 2px; /* Thinner line */
  background-color: var(--primary-color);
  border-radius: 2px;
}

.navbar ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navbar li {
  margin: 0.5rem 0; /* Reduced from 0.75rem */
}

/* Navbar link styles */
.navbar a {
  color: var(--text-color-medium);
  text-decoration: none;
  font-size: 0.9rem; /* Reduced from 1rem */
  font-weight: 500;
  display: flex;
  align-items: center;
  padding: 0.5rem 0.7rem; /* Reduced padding */
  border-radius: 6px;
  transition: all 0.25s ease;
  position: relative;
  overflow: hidden;
}

.navbar a:hover, 
.navbar a.active {
  background-color: var(--accent-soft);
  color: var(--primary-color);
  transform: none;
  text-decoration: none;
}

.navbar a.active {
  font-weight: bold;
}

/* Dark theme navbar overrides */
.dark-theme .navbar a {
  color: rgba(255, 255, 255, 0.75);
}

.dark-theme .navbar a:hover,
.dark-theme .navbar a.active {
  background-color: var(--accent-soft);
  color: rgba(255, 255, 255, 0.75);
}

/* Add styling for nav-icon */
.nav-icon {
  margin-right: 0.5rem;
  font-size: 0.85rem;
  color: var(--text-secondary);
  transition: color 0.25s ease;
}

.navbar a:hover .nav-icon, 
.navbar a.active .nav-icon {
  color: var(--primary-color);
}

/* Dark theme icon overrides */
.dark-theme .navbar a:hover .nav-icon,
.dark-theme .navbar a.active .nav-icon {
  color: var(--primary-color);
}

.theme-toggle {
  padding: 0.75rem 1.25rem;
  background-color: transparent;
  color: var(--text-primary);
  border: 1px solid var(--border-color);
  cursor: pointer;
  border-radius: 6px;
  transition: all 0.25s ease;
  font-weight: 500;
  margin-top: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 0.85rem;
}

.theme-toggle:hover {
  background-color: var(--accent-soft);
  color: var(--primary-color);
  border-color: var(--primary-color);
}

/* Content container with cleaner spacing */
.content-container {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
  max-width: 1200px;
  margin: 0 auto;
}

/* Remove padding for demo page */
.demo-apps-container .content-container,
.content-container:has(.demo-apps-container) {
  padding: 0;
}

/* Projects Page Styles */
.projects-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.projects-page h2 {
  margin-bottom: 1.5rem;
  text-align: center;
}

/* Category Grid */
.category-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1rem;
  margin-bottom: 2rem;
}

.category-box {
  background-color: var(--card-bg-color);
  border: 2px solid var(--card-border-color);
  border-radius: 8px;
  padding: 1.5rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 120px;
}

.category-box:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px -3px var(--card-hover-shadow);
}

.category-box.selected {
  border-color: var(--card-selected-border);
  box-shadow: 0 0 0 2px var(--card-selected-border);
}

.category-box h3 {
  font-size: 1.1rem;
  margin: 0;
}

/* Project List */
.project-list-container {
  margin-top: 2rem;
}

.project-list-container h3 {
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid var(--card-border-color);
}

.project-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.project-item {
  border-radius: 6px;
  overflow: hidden;
  background-color: var(--project-item-bg);
  transition: all 0.2s ease;
}

.project-header {
  display: flex;
  align-items: center;
  padding: 0.75rem 1rem;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.project-header:hover {
  background-color: var(--project-item-hover);
}

.expand-icon {
  margin-right: 0.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
}

.project-header h4 {
  margin: 0;
  font-size: 1rem;
  font-weight: 500;
}

.project-details {
  padding: 1rem;
  background-color: var(--project-details-bg);
  border-top: 1px solid var(--card-border-color);
}

.project-details p {
  margin: 0;
  line-height: 1.5;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .category-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }
}

@media (max-width: 768px) {
  .category-grid {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(8, 1fr);
  }
}

/* Original project grid styles */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.project-box {
  background-color: var(--card-bg-color);
  border: 1px solid var(--card-border-color);
  border-radius: var(--border-radius);
  overflow: hidden;
  transition: all 0.25s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.project-box:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 20px var(--card-hover-shadow);
}

.project-box img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.project-box .project-content {
  padding: 1.25rem;
  flex: 1;
}

.project-box h3 {
  font-size: 1.1rem;
  margin-bottom: 0.75rem;
  color: var(--text-primary);
  font-weight: 600;
}

.project-box p {
  font-size: 0.9rem;
  color: var(--text-secondary);
}

/* Project tags with a cleaner look */
.project-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
}

.project-tag {
  display: inline-block;
  padding: 0.35rem 0.7rem;
  background-color: var(--accent-soft);
  color: var(--primary-color);
  border-radius: 30px;
  font-size: 0.75rem;
  font-weight: 500;
}

/* Learn more link with a cleaner style */
.learn-more-link {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--primary-color);
  text-decoration: none;
  font-size: 0.85rem;
  font-weight: 500;
  margin-top: 1rem;
  padding: 0.5rem 0;
  transition: all 0.25s ease;
  position: relative;
}

.learn-more-link::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 1px;
  background-color: var(--primary-color);
  transition: width 0.25s ease;
}

.learn-more-link:hover::after {
  width: 100%;
}

/* Responsive media queries */
@media (max-width: 1024px) {
  .about-section {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
  .about-image {
    max-width: 350px;
    margin: 0 auto;
  }
}

@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
  }
  
  .navbar {
    width: 100%;
    height: auto;
    padding: 1rem;
    position: relative;
  }
  
  .content-container {
    padding: 1.5rem;
  }
}

/* About section improvements */
.about-section {
  display: grid;
  grid-template-columns: 1fr 2fr;
  gap: 3rem;
  align-items: center;
  padding: 2rem 0;
}

.about-image {
  position: relative;
  width: 100%;
}

.about-image img {
  width: 100%;
  height: auto;
  border-radius: var(--border-radius);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.about-content h2 {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  color: var(--primary-color);
  position: relative;
}

.about-content h2::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 60px;
  height: 3px;
  background-color: var(--text-color);
  border-radius: 3px;
}

.about-content p {
  margin-bottom: 1.5rem;
  line-height: 1.8;
  font-size: 1.05rem;
}

/* Demo Apps Page Styles */
.demo-apps-container {
  display: flex;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

.demo-sidebar {
  width: 240px; /* Reduced from 280px */
  background-color: var(--nav-bg-color);
  border-right: 1px solid var(--card-border-color);
  padding: 1.5rem;
  overflow-y: auto;
  height: 100%;
}

.demo-sidebar h3 {
  margin-top: 0.85rem;
  margin-bottom: 1.5rem;
  padding-bottom: 0;
  border-bottom: none;
  font-size: 0.95rem;
  color: var(--text-primary);
  font-weight: 600;
  letter-spacing: -0.02em;
}

.demo-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.demo-item {
  padding: 0.5rem 0.7rem;
  margin-bottom: 0.4rem;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.25s ease;
  display: flex;
  align-items: center;
  font-size: 0.85rem;
  color: var(--nav-text-color);
}

.demo-item:hover {
  background-color: var(--accent-soft);
  color: var(--primary-color);
}

.demo-item.active {
  background-color: var(--accent-soft);
  color: var(--primary-color);
  font-weight: 500;
  border-left: none;
}

.demo-icon {
  margin-right: 0.65rem;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-tertiary);
  font-size: 0.9rem;
  transition: color 0.25s ease;
}

.demo-item:hover .demo-icon,
.demo-item.active .demo-icon {
  color: var(--primary-color);
}

.demo-icon.large {
  font-size: 1.3rem;
  margin-right: 0.85rem;
  color: var(--primary-color);
}

.image-app-title h3 {
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0;
  color: var(--text-primary);
}

.image-app-controls {
  display: flex;
  gap: 10px;
}

.app-control-button {
  background: rgba(0, 0, 0, 0.05);
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.2s ease;
}

.app-control-button:hover {
  background: rgba(255, 255, 255, 0.3);
}

.image-app-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  flex: 1;
  overflow: hidden;
  height: calc(100% - 56px);
}

.image-preview-pane {
  padding: 16px;
  display: flex;
  flex-direction: column;
  border-right: 1px solid var(--border-color);
  overflow-y: auto;
}

.results-pane {
  padding: 16px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
}

.upload-area {
  border: 2px dashed var(--border-color);
  border-radius: 8px;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.02);
  cursor: pointer;
  margin-bottom: 16px;
}

.upload-area:hover {
  background-color: rgba(0, 0, 0, 0.04);
  border-color: var(--primary-light);
}

.upload-placeholder {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-top: 15px;
}

.upload-icon-large {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 15px;
}

.image-recognition-status {
  margin-top: 16px;
  padding: 10px;
  border-radius: 6px;
  font-size: 0.85rem;
  display: flex;
  align-items: center;
  gap: 8px;
  background-color: rgba(67, 97, 238, 0.1);
  color: var(--primary-color);
}

.processing-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(67, 97, 238, 0.2);
  border-left-color: var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.results-section {
  margin-bottom: 20px;
}

.results-section-title {
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 10px;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  gap: 6px;
}

.confidence-bar {
  height: 8px;
  background-color: #e9ecef;
  border-radius: 4px;
  margin-top: 4px;
  overflow: hidden;
}

.confidence-level {
  height: 100%;
  background-color: var(--primary-color);
  border-radius: 4px;
}

.detection-item {
  padding: 10px;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  margin-bottom: 10px;
  background-color: var(--surface-color);
}

.detection-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 8px;
}

.detection-name {
  font-weight: 600;
}

.detection-confidence {
  font-size: 0.85rem;
  color: var(--text-secondary);
}

/* Updated image container for app UI */
.image-container-app {
  position: relative;
  width: 100%;
  height: 200px;
  overflow: hidden;
  border-radius: 6px;
  border: 1px solid var(--border-color);
  margin-bottom: 16px;
}

.placeholder-image-app {
  width: 100%;
  height: 100%;
  background-color: #f8f8f8;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  border-radius: 6px;
  overflow: hidden;
}

.dark-theme .placeholder-image-app {
  background-color: #1a1a1a;
}

/* Empty results and analysis summary styles */
.empty-results {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 30px 20px;
  color: var(--text-secondary);
  font-style: italic;
