  border-top-left-radius: 0;
  margin-right: auto;
}

.user-message .message-content {
  border-top-right-radius: 0;
  margin-left: auto;
  background-color: var(--primary-color);
  color: white;
}

.code-block {
  margin: 1rem 0;
