  height: 200px;
  padding: 0 10px;
}

.chart-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 14%;
}

.chart-bar {
  width: 100%;
  background-color: var(--primary-color);
  border-radius: 3px 3px 0 0;
  position: relative;
  transition: height 0.5s ease;
}

.bar-value {
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.7rem;
  color: var(--text-secondary);
}

.bar-label {
  margin-top: 0.5rem;
  font-size: 0.8rem;
  color: var(--text-secondary);
}

.chat-input {
  display: flex;
