}

.action-button.secondary:hover {
  background-color: var(--bg-color);
}

/* Risk Factors Styles */
.factors-container {
  background-color: var(--surface-color);
  border-radius: var(--border-radius);
  border: 1px solid var(--card-border-color);
  overflow: hidden;
  transition: all 0.3s ease;
