}

.history-item.defaulted .history-status {
  color: #ef4444;
}

.history-balance {
  font-weight: 600;
  color: var(--text-primary);
  text-align: right;
  white-space: nowrap;
}

.profile-item {
  display: flex;
  padding: 1rem 1.2rem;
  align-items: center;
  gap: 1.2rem;
  margin-bottom: 0.9rem;
  border-radius: 8px;
  background-color: var(--card-bg-color);
  border: 1px solid var(--card-border-color);
}

.profile-item:hover {
  /* Remove hover effects */
  background-color: var(--card-bg-color);
  transform: none;
}

.profile-outcome {
  padding: 0.4rem 0.8rem;
  border-radius: 4px;
  font-weight: 500;
  font-size: 0.8rem;
  min-width: 100px;
  text-align: center;
}

.profile-outcome.rejected {
  background-color: rgba(239, 68, 68, 0.15);
  color: #ef4444;
}

.profile-outcome.conditional {
  background-color: rgba(245, 158, 11, 0.15);
  color: #f59e0b;
}

.profile-details {
  flex: 1;
}

.profile-score {
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.2rem;
}

.profile-factors {
  color: var(--text-secondary);
