/* Risk Score Gauge Styles */
.risk-gauge-container {
  padding: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gauge {
  position: relative;
  width: 200px;
  height: 100px;
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gauge-bg {
  position: relative;
  width: 200px;
  height: 100px;
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
  background: linear-gradient(90deg, #28a745 0%, #ffc107 50%, #dc3545 100%);
  overflow: hidden;
}

.gauge-fill {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 200px;
  background-color: var(--card-bg-color);
  border-radius: 50%;
  transform: translateY(50%);
}

.gauge-needle {
  position: absolute;
  bottom: 0;
  left: 100px;
  width: 2px;
  height: 80px;
  background-color: #333;
  transform-origin: bottom center;
  transform: rotate(0deg);
  transition: transform 1s ease-in-out;
}

.gauge-center {
  position: absolute;
  bottom: 0;
  left: 100px;
  width: 10px;
  height: 10px;
  background-color: #333;
  border-radius: 50%;
  transform: translate(-50%, 50%);
}

.risk-score {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  margin-top: 1rem;
}

.risk-label {
  font-size: 1rem;
  font-weight: 500;
  margin-top: 0.2rem;
}

