/* Risk Assessment Chart Fixes */
.risk-score-container {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gauge-visualization {
  position: relative;
  width: 220px;
  height: 140px;
  margin: 1rem auto 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.gauge-arc {
  position: relative;
  width: 200px;
  height: 100px;
  margin: 0 auto;
  overflow: hidden;
}

.gauge-scale {
  position: absolute;
  top: 0;
  left: 0;
  width: 200px;
  height: 100px;
  border-top-left-radius: 100px;
  border-top-right-radius: 100px;
}

.gauge-scale.low {
  background-color: #28a745;
  clip-path: polygon(0 0, 33% 0, 33% 100%, 0% 100%);
  z-index: 1;
}

.gauge-scale.medium {
  background-color: #ffc107;
  clip-path: polygon(33% 0, 67% 0, 67% 100%, 33% 100%);
  z-index: 1;
}

.gauge-scale.high {
  background-color: #dc3545;
  clip-path: polygon(67% 0, 100% 0, 100% 100%, 67% 100%);
  z-index: 1;
}

.gauge-needle {
  position: absolute;
  bottom: 0;
  left: 100px;
  width: 3px;
  height: 85px;
  background-color: #333;
  transform-origin: bottom center;
  transform: rotate(-90deg);
  transition: transform 1.5s cubic-bezier(0.17, 0.67, 0.21, 1.03);
  z-index: 3;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  border-radius: 3px;
}

.gauge-center {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 100px;
  height: 100px;
  background-color: var(--card-bg-color);
  border-radius: 50%;
  transform: translate(-50%, 50%);
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: 1px solid var(--card-border-color);
}

